'use server';

/**
 * @fileOverview AI flow for generating personalized sustainable farming tips.
 *
 * - generateSustainableTips - A function that generates personalized sustainable farming tips.
 * - SustainableTipsInput - The input type for the generateSustainableTips function.
 * - SustainableTipsOutput - The return type for the generateSustainableTips function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const SustainableTipsInputSchema = z.object({
  cropType: z.string().describe('The type of crop being grown.'),
  landType: z.string().describe('The type of land being farmed.'),
  currentTasks: z.string().describe('The current tasks being performed.'),
});
export type SustainableTipsInput = z.infer<typeof SustainableTipsInputSchema>;

const SustainableTipsOutputSchema = z.object({
  tip: z.string().describe('A single, short, personalized sustainable farming tip.'),
});
export type SustainableTipsOutput = z.infer<typeof SustainableTipsOutputSchema>;

export async function generateSustainableTips(input: SustainableTipsInput): Promise<SustainableTipsOutput> {
  return generateSustainableTipsFlow(input);
}

const prompt = ai.definePrompt({
  name: 'sustainableTipsPrompt',
  input: {schema: SustainableTipsInputSchema},
  output: {schema: SustainableTipsOutputSchema},
  prompt: `You are an expert in sustainable farming practices. Based on the farmer's crop type, land type, and current tasks, provide one short, personalized, and actionable sustainable farming tip. The tip should be concise and easy to understand.

Crop Type: {{{cropType}}}
Land Type: {{{landType}}}
Current Tasks: {{{currentTasks}}}

Tip:`,
});

const generateSustainableTipsFlow = ai.defineFlow(
  {
    name: 'generateSustainableTipsFlow',
    inputSchema: SustainableTipsInputSchema,
    outputSchema: SustainableTipsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
