// Verify Land Doc Authenticity

'use server';

/**
 * @fileOverview Verifies the authenticity of land ownership documents using AI.
 *
 * - verifyLandDocAuthenticity - A function that handles the land document authenticity verification process.
 * - VerifyLandDocAuthenticityInput - The input type for the verifyLandDocAuthenticity function.
 * - VerifyLandDocAuthenticityOutput - The return type for the verifyLandDocAuthenticity function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const VerifyLandDocAuthenticityInputSchema = z.object({
  photoDataUri: z
    .string()
    .describe(
      "A photo of a land ownership document, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
});
export type VerifyLandDocAuthenticityInput = z.infer<typeof VerifyLandDocAuthenticityInputSchema>;

const VerifyLandDocAuthenticityOutputSchema = z.object({
  isAuthentic: z
    .boolean()
    .describe('Whether or not the land ownership document is authentic.'),
  confidenceScore: z
    .number()
    .describe(
      'A score between 0 and 1 indicating the confidence level of the authenticity verification.'
    ),
  reason: z
    .string()
    .describe('The reasoning behind the authenticity determination.'),
});
export type VerifyLandDocAuthenticityOutput = z.infer<
  typeof VerifyLandDocAuthenticityOutputSchema
>;

export async function verifyLandDocAuthenticity(
  input: VerifyLandDocAuthenticityInput
): Promise<VerifyLandDocAuthenticityOutput> {
  return verifyLandDocAuthenticityFlow(input);
}

const prompt = ai.definePrompt({
  name: 'verifyLandDocAuthenticityPrompt',
  input: {schema: VerifyLandDocAuthenticityInputSchema},
  output: {schema: VerifyLandDocAuthenticityOutputSchema},
  prompt: `You are an AI expert in verifying the authenticity of land ownership documents.

You will receive a photo of a document and must determine if it is authentic or not.

You will provide a confidence score between 0 and 1 (where 1 is 100% confident) and a reason for your determination.

Photo: {{media url=photoDataUri}}`,
});

const verifyLandDocAuthenticityFlow = ai.defineFlow(
  {
    name: 'verifyLandDocAuthenticityFlow',
    inputSchema: VerifyLandDocAuthenticityInputSchema,
    outputSchema: VerifyLandDocAuthenticityOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
