'use server';
/**
 * @fileOverview AI-powered validation of farm and crop context based on uploaded photos.
 *
 * - validateFarmCropContext - A function that validates if the uploaded photo matches the selected crop and task context.
 * - ValidateFarmCropContextInput - The input type for the validateFarmCropContext function.
 * - ValidateFarmCropContextOutput - The return type for the validateFarmCropContext function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const ValidateFarmCropContextInputSchema = z.object({
  photoDataUri: z
    .string()
    .describe(
      "A photo of the field or crops, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  cropType: z.string().describe('The type of crop selected by the farmer.'),
  taskDescription: z.string().describe('The description of the task completed by the farmer.'),
});
export type ValidateFarmCropContextInput = z.infer<typeof ValidateFarmCropContextInputSchema>;

const ValidateFarmCropContextOutputSchema = z.object({
  isValidContext: z
    .boolean()
    .describe(
      'Whether the photo content matches the selected crop and task context.'
    ),
  reason: z
    .string()
    .describe('The reason why the photo is considered valid or invalid.'),
});
export type ValidateFarmCropContextOutput = z.infer<typeof ValidateFarmCropContextOutputSchema>;

export async function validateFarmCropContext(
  input: ValidateFarmCropContextInput
): Promise<ValidateFarmCropContextOutput> {
  return validateFarmCropContextFlow(input);
}

const prompt = ai.definePrompt({
  name: 'validateFarmCropContextPrompt',
  input: {schema: ValidateFarmCropContextInputSchema},
  output: {schema: ValidateFarmCropContextOutputSchema},
  prompt: `You are an AI assistant specializing in validating farm and crop contexts based on uploaded photos.

You will receive a photo, the selected crop type, and the description of a completed task.
Your goal is to determine whether the content of the photo matches the provided crop and task context.

Here is the information:
Crop Type: {{{cropType}}}
Task Description: {{{taskDescription}}}
Photo: {{media url=photoDataUri}}

Based on the given information, determine if the photo provides sufficient evidence for the completed task related to the specified crop.
Explain your reasoning for determining if the photo is considered valid or invalid in the "reason" field. Set the isValidContext field to true if it is, otherwise to false.
`,
});

const validateFarmCropContextFlow = ai.defineFlow(
  {
    name: 'validateFarmCropContextFlow',
    inputSchema: ValidateFarmCropContextInputSchema,
    outputSchema: ValidateFarmCropContextOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
